

.contenedorApp{
	padding: 15px;
border-radius: 24px;
background: #008f39;
box-shadow:  8px 8px 16px #00712d,
             -8px -8px 16px #00ad45;
             min-height: 90vh;


}

.contenedorApp ul{
	list-style: none;
	display: flex;
	background: #000;
	padding: 10px;
}

.contenedorApp li{
	margin: auto;
	color: #fff;
}

.contenedorApp li:hover{
	border-radius: 50px;
background: green;
cursor: pointer;

}

.contenedorApp .Encabezado{
	margin-bottom: 10px;
	text-align: center;
}

.contenedorApp .Encabezado a{
	text-decoration: none;
	color: #fff;
	font-weight: bold;
}

.contenedorApp .Tareas span{
	max-width: 100%;
	padding: 5px;

	text-align: center;
}

.contenedorApp .Tareas{
	margin-top: 10px;
	text-align: center;
}

.contenedorApp .Tareas button{
	outline: none;
	background: #000;
	color: #fff;
	margin: 3px;
	padding: 10px;
	border: none;

}
.contenedorApp .Tareas button:hover{
	background: #fff;
	color: #000;
}


.contenedorApp .Tareas input[type="text"]{
	width: 90%;
	height: 30px;
	border: none;
	outline: none;
	padding: 3px;
border-radius: 50px;
background: #e0e0e0;
box-shadow: inset 20px 20px 60px #c7c7c7,
            inset -20px -20px 60px #f9f9f9;
}

.contenedorApp .contenidoClima{
	display: flex;

	min-height: 73vh;
}

@media only screen and (max-width: 500px){
.contenedorApp .contenidoClima{
	flex-direction: column;

}

}
