*{
	
}

.contenendorNavLateral{
	min-height: 73vh;
	bottom: 0;
	width: 25%;
	display: inline-block;
	max-width: 25%;
}

.BarraLatetal{
	width: 100%;
	height: 100%
}

.listaLateral{
	height: 100%;
	display: flex;
	background: red;
	justify-content: space-evenly;
	flex-direction: column;	
}

.BarraLatetal button{
	background: none;
	color: #fff;
	font-size: 16px;
}

.BarraLatetal button:hover{
	cursor: pointer;
		border-radius: 50px;
background: green;

}

@media only screen and (max-width: 500px) {
.contenendorNavLateral{
	min-height: 50px;
	min-width: 100%;

}	

.BarraLatetal{
	min-height: 30px;
	width: 100%;

}

.listaLateral{
	flex-direction: row;
	width: 100%;

}

}



@media only screen and (max-width: 423px) {
.contenendorNavLateral{
	display: none;
}

}






